*,
*::before,
*::after {
  box-sizing: border-box;
}

ol {
  padding: 0;
}

h1 {
  font-family: 'Do Hyeon', sans-serif;
  margin: 0;
  font-weight: 400;
}

#app-root {
  display: flex;
  justify-content: center;
}

.app {
  width: 600px;
  margin: 0 1rem;
}

.app__heading {
  font-size: 3rem;
  text-align: center;
}

.controls {
  max-width: 100%;
  margin: 0 auto;
}

.controls__input,
.controls__button {
  display: block;
  width: 100%;
  padding: 0.5rem;
  font-weight: bold;
  font-size: 1rem;
  outline: none;
  border: 1px solid #000;
  border-radius: 0.3rem;
}

.controls__button {
  margin-top: 0.5rem;
  background-color: #ffd503;
  cursor: pointer;
}

.results {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 1rem;
}

.option-list {
  flex: 1;
  background-color: #424242;
  color: #fff;
  border: 5px solid #b1a879;
  min-width: 200px;
}

.option-list__info {
  text-align: center;
  text-transform: uppercase;
  font-family: 'Do Hyeon', sans-serif;
  padding: 0 1rem;
}

.option-list__list {
  padding: 0 10px;
  list-style-position: inside;
}

.option-list__item {
  cursor: pointer;
}

.option-list__item:hover {
  color: #b1a879;
}

.result-fields {
  flex: 2;
  margin-left: 1rem;
}

.result-fields__name {
  font-size: 2rem;
  font-family: 'Do Hyeon', sans-serif;
  margin: 0;
  text-align: center;
}

.result-fields__image {
  display: block;
  margin: 0 auto;
  border-radius: 0.5rem;
  border: 5px solid #b1a879;
  margin-top: 1rem;
}

.result-fields__instruction {
  text-align: center;
  background-color: #424242;
  color: #fff;
  border: 5px solid #b1a879;
  border-radius: 0.5rem;
  padding: 1rem;
}

.ingredients {
  display: flex;
  justify-content: center;
}

.ingredients__list-name {
  list-style: none;
  padding: 0 0.5rem;
  font-family: 'Do Hyeon', sans-serif;
}

.ingredients__list-qty {
  list-style: none;
  padding: 0 0.5rem;
  margin-left: 1rem;
  font-family: 'Do Hyeon', sans-serif;
}

.ingredients__item {
  margin-top: 0.2rem;
}

.ingredients__item__selectable {
  cursor: pointer;
}

.ingredients__item__selectable:hover {
  color: #b1a879;
}

.ingredient-info__heading,
.ingredient-info__name {
  font-size: 2rem;
  font-family: 'Do Hyeon', sans-serif;
  margin: 0;
  text-align: center;
}

.ingredient-info__name {
  color: #b1a879;
}

.ingredient-info__image {
  display: block;
  margin: 0 auto;
  margin-top: 1rem;
  border-radius: 0.5rem;
  border: 5px solid #b1a879;
}

.ingredient-info__data {
  text-align: center;
  background-color: #424242;
  color: #fff;
  border: 5px solid #b1a879;
  border-radius: 0.5rem;
  padding: 1rem;
}

.message {
  font-size: 1.5rem;
  font-family: 'Do Hyeon', sans-serif;
  margin: 1rem;
  text-align: center;
}
